import { createRouter, createWebHistory } from 'vue-router';
import HomePage from "@/views/HomePage.vue"
import MonthPage from "@/views/MonthPage.vue"
import AboutMaria from "@/views/AboutMaria.vue"

const routes = [
  { path: '/0month', component: MonthPage, props: { month: '0 мес' } },
  { path: '/1month', component: MonthPage, props: { month: '1 мес' } },
  { path: '/2month', component: MonthPage, props: { month: '2 мес' } },
  { path: '/3month', component: MonthPage, props: { month: '3 мес' } },
  { path: '/about', component: AboutMaria },
  { path: '/', redirect: '/home' },
  {
    path: "/home",
    name: "home",
    component: HomePage,
  },
  {
    path: "/about",
    name: "about",
    component: AboutMaria,
  },
];

const router = createRouter({
  history: createWebHistory('https://llinch.github.io/'),
  routes,
});

export default router;
